.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#text{
  text-align: center;
  padding: 10px;


  /* font-family: "Montserrat";
  font-optical-sizing: auto;
  font-weight: weight;
  font-style: normal; */

  font-family: "Libertinus Mono";
  font-weight: 400;
  font-style: normal;
  
}

.todoitem{

  padding-left: 5%;
  padding: 0;
  word-wrap: break-word;
  /* display: flex; */
  

  font-family: "Montserrat";
  font-optical-sizing: auto;
  font-weight: weight;
  font-style: normal;

  
}
.container{
  min-height: 100vh;
  word-wrap: break-word;
  white-space: normal;
  /* max-width: vw; */
  /* margin: 10px; */
}
.addtodo{
  margin: 5px;
  padding: 5px;
  width: 60%;
}
#add{
  display: flex;
  justify-content: center;
}
#btn{
  display: flex;
  justify-content: center;
}
#checkbox{
  display: flex;
  justify-content: space-between;
  /* word-wrap: break-word; */
  /* flex-wrap: wrap; */
}
/* #titlebox{
  word-wrap: break-word;
  max-width: 70vw;
} */

#taskdone{
  text-decoration: line-through;
  color:  #6c757d;

}
#btnn{
  /* background: white; */
  width:21%;
  /* max-height: 20%; */
  
}

#tbtn{
  width: 21%;
  /* max-height: 10vh; */
}
#btnn:hover{
  background-color: white;
  color: #6c757d;
}
#tbtn:hover{
  background-color: #6c757d ;
  color: white;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
